const form = require('@/components/common/tui-validation/tui-validation.js');
var { parseTime } = require('wx-tool');

export default {
	data() {
		return {
			isUrgent: false,
			message: undefined,
			files: [] //最多上传9张图片
		};
	},
	methods: {
		formSubmit: function(e) {
			//表单规则
			let rules = [
				{
					name: 'message',
					rule: ['required', 'maxLength:500'],
					msg: ['请输入留言', '留言不能超过500个字']
				}
			];
			//进行表单检查
			let formData = e.detail.value;
			let checkRes = form.validation(formData, rules);
			if (!checkRes) {
				let db = wx.cloud.database({
					env: 'jnmc-ronxp'
				});

				db.collection('message').add({
					data: {
						message: this.message,
						is_urgent: this.isUrgent,
						create_time: parseTime(Date.parse(new Date()), '{y}-{m}-{d} {h}:{i}'),
						username: '天道酬勤'
					},
					success: res => {
						uni.showToast({
							title: '留言成功',
							icon: 'success',
							success: function() {
								setTimeout(function() {
									uni.switchTab({
										url: './index',
										success: function(e) {
											const page = getCurrentPages().pop();
											if (!page) return;
											page.onLoad();
										}
									});
								}, 1000);
							}
						});
					},
					fail: err => {
						console.log('留言失败', err);
					}
				});
			} else {
				uni.showToast({
					title: checkRes,
					icon: 'none'
				});
			}
		},
		handleSwitchUrgent(e) {
			this.isUrgent = e.detail.value;
		},
		chooseImage: function(e) {
			let that = this;
			if (that.files.length >= 9) {
				this.tui.toast('最多上传9张图片');
				return;
			}
			wx.chooseImage({
				count: 9 - that.files.length,
				sizeType: ['original', 'compressed'], // 可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album', 'camera'], // 可以指定来源是相册还是相机，默认二者都有
				success: function(res) {
					// 返回选定照片的本地文件路径列表，tempFilePath可以作为img标签的src属性显示图片
					that.files = that.files.concat(res.tempFilePaths);
					//上传功能已去除
					//...
				}
			});
		},
		previewImage: function(e) {
			uni.previewImage({
				current: e.currentTarget.id, // 当前显示图片的http链接
				urls: this.files // 需要预览的图片http链接列表
			});
		},
		deleteImage: function(e) {
			const index = e.index;
			this.files.splice(index, 1);
		}
	}
};
